.rslides {
  position: relative;
  list-style: none;
  overflow: hidden;
  width: 100%;
  height: 100%;
  min-height: 200px;
  min-width: 324px;
  padding: 0;
  margin: 0;
}
.rslides li {
  -webkit-backface-visibility: hidden;
  position: absolute;
  display: none;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.rslides li:first-child {
  position: relative;
  display: block;
  float: left;
}
.rslides li img {
  display: block;
  width: 100%;
  height: auto;
  max-height: 332px;
  float: left;
  max-width: 500px;
  border: none;
}
.rslides li div {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.1em;
  color: #ffcc00;
  text-align: left;
  padding: 0 0 0 1em;
  line-height: 2em;
  z-index: 2;
  font-size: 13px;
}
.rslides li:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.1em;
  background-color: black;
  opacity: .6;
}
